        const stepOneNext = document.querySelector('.review__btn.step-1 .next');
        const stepTwoNext = document.querySelector('.review__btn.step-2 .next');
        const stepOneMid = document.querySelector('.review__mid-step-1.mob');
        const stepTwoMid = document.querySelector('.review__mid-step-2.mob');
        const stepThreeMid = document.querySelector('.review__mid-step-3.mob');

        stepOneNext.addEventListener('click', () => {
            stepOneMid.classList.remove('active')
            stepTwoMid.classList.add('active')
            reviewBlock.classList.add('step-2');
        })
